import React, { useEffect, useState } from "react";

const API_URL = "/api/assignments/";

function AssignmentCalendar() {
  const [assignments, setAssignments] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch(API_URL)
      .then(res => res.json())
      .then(setAssignments)
      .catch(() => setAssignments([]))
      .finally(() => setLoading(false));
  }, []);

  // Simple calendar: group assignments by date (assigned_at)
  const grouped = assignments.reduce((acc, a) => {
    const date = a.assigned_at ? a.assigned_at.slice(0, 10) : "Unknown";
    if (!acc[date]) acc[date] = [];
    acc[date].push(a);
    return acc;
  }, {});

  if (loading) return <div>Loading calendar...</div>;

  return (
    <div>
      <h2>Assignment Calendar (by Assigned Date)</h2>
      {Object.keys(grouped).sort().map(date => (
        <div key={date} style={{ marginBottom: 16 }}>
          <strong>{date}</strong>
          <ul>
            {grouped[date].map(a => (
              <li key={a.id}>
                {a.status}: Assignment #{a.id} (Campaign {a.campaign}, Station {a.station || "-"})
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  );
}

export default AssignmentCalendar;
